.btn-change {
    width: 250px;
    font-size: 18px;
    margin: 0 5px;
    height: 35px;
    border: 2px solid #08dbff;
    background: #241B5E;
    box-shadow: 0 0 3px 1px #08DBFF;
    transition: 0.1s;
    border-radius: 10px;

    &:hover {
        border: 2px solid #241B5E;
        background: #08DBFF;
        box-shadow: 0 0 5px 2px #08DBFF;
    }

    &:active {
        border: 2px solid #241B5E;
        background: #08DBFF;
        box-shadow: 0 0 5px 2px #08DBFF;
    }

    @media (max-width: 768px) {
        margin-bottom: 10px;
        
        &:hover {
            border: 2px solid #08dbff;
            background: #241B5E;
            box-shadow: 0 0 3px 1px #08DBFF;
        }

        &:active {
            border: 2px solid #241B5E;
            background: #08DBFF;
            box-shadow: 0 0 5px 2px #08DBFF;
        }
    }
}