.auth {
    height: 100vh;
    width: 100%;
    background: #3B3273;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    .btn-signIn, .btn-registration {
        display: inline-block;
        padding: 5px 15px;
        background: transparent;
        border-radius: 10px;
        color: #fff;
        border: 2px solid #08DBFF;
        cursor: pointer;
        font-size: 18px;
        margin: 0 5px;
        outline: none;
        box-shadow: 0 0 5px 2px #241b5e;

        &.active {
            border: 2px solid #241B5E;
            background: #08DBFF;
            box-shadow: 0 0 5px 2px #08DBFF;
        }
    }
}