.postAddForm {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: absolute;
    background: rgba(59, 50, 115, 0.749);
    top: 0;
    left: 0;
    z-index: 5;

    .close {
        position: absolute;
        font-size: 24px;
        font-weight: bold;
        cursor: pointer;
        color: white;
        top: -5px;
        right: 10px;
        transition: 0.1s;

        &:hover {
            color: rgb(168, 168, 168);
        }
    }

    form {
        width: 350px;
        min-height: 250px;
        background: #241B5E;
        padding: 20px;
        border-radius: 10px;

        input {
            display: block;
            width: 270px;
            width: 100%;
            height: 35px;
            font-size: 22px;
            background: #ffffffe8;
            padding-left: 10px;
            margin: 2px auto;
            border-radius: 10px;
            border: 2px solid #08dbff;
        }

        textarea {
            width: 100%;
            resize: none;
            font-size: 22px;
            background: #ffffffe8;
            border-radius: 10px;
            padding: 5px;
            border: 2px solid #08dbff;
        }

        input[type='submit'] {
            padding: 0;
            cursor: pointer;
        }
    }
}