.body {
    display: flex;
    max-width: 1235px;
    margin: auto;
    
    .right-column {
        width: 100%;
        margin-left: 15px;
        

        .border {
            border: 2px solid #08DBFF;
            border-right: 1px solid #08DBFF;
            border-radius: 10px 5px 5px 10px;
            padding: 10px 15px;
            height: 600px;
            max-height: 590px;
            overflow: auto;
            
            &::-webkit-scrollbar {
                width: 7px;
                background-color: #08DBFF;
                border-radius: 10px;
            }
            &::-webkit-scrollbar-thumb {
                border: 1px solid #08DBFF;
                background-color: #3B3273;
                border-radius: 10px;
            }
            &::-webkit-scrollbar-track {
                box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.2) inset;
                -webkit-box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.2) inset;
                background-color: #08DBFF;
                border-radius: 10px;
            }

            .card {
                background: #241B5E;
                box-shadow: 0 0 5px 3px #08DBFF;
                border-radius: 10px;
                padding: 10px;
                margin-top: 5px;
                display: block;
                margin-bottom: 20px;

                &:last-child {
                    margin-bottom: 5px;
                }

                .dataId {
                    display: flex;
                    flex-wrap: wrap;
                    justify-content: space-between;

                    .name, .age, .side {
                        margin: 0 5px;
                        width: 100%;
                        max-width: 280px;
                        height: 40px;
                        color: #fff;
                        background: transparent;
                        border: 2px solid #08dbff;
                        border-radius: 10px;
                        font-size: 22px;
                        text-align: center;

                        @media (max-width: 1100px) {
                            margin-bottom: 10px;

                            &:last-child {
                                margin-bottom: 0;
                                margin: auto;
                            }
                        }

                        @media (max-width: 340px) {
                            font-size: 18px;
                        }
                    }
                    
                    @media (max-width: 800px) {
                        justify-content: center;
                    }
                }

                .change_btns {
                    display: flex;
                    flex-wrap: wrap;
                    justify-content: space-between;
                    margin-top: 20px;

                    @media (max-width: 800px) {
                        justify-content: center;

                        .change_btn {
                            margin-bottom: 10px;

                            &:last-child {
                                margin-bottom: 0;
                            }
                        }
                    }
                }

                .description-title {
                    display: inline-block;
                    font-size: 22px;
                    position: relative;
                    margin: 7px 0;
                }
                .description {
                    padding: 5px 10px;
                    height: 220px;
                    border-radius: 10px 5px 5px 10px;
                    overflow: auto;
                    font-size: 22px;
                    border: 2px solid #08dbff;
                    border-right: 1px solid hsl(189, 100%, 52%);

                    &::-webkit-scrollbar {
                        width: 7px;
                        background-color: #08DBFF;
                        border-radius: 10px;
                    }
                    &::-webkit-scrollbar-thumb {
                        border: 1px solid #08DBFF;
                        background-color: #3B3273;
                        border-radius: 10px;
                    }
                    &::-webkit-scrollbar-track {
                        box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.2) inset;
                        -webkit-box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.2) inset;
                        background-color: #08DBFF;
                        border-radius: 10px;
                    }
                }
            }
            .hide {
                display: none;
            }
        }
    }

    @media (max-width: 560px) {
        flex-wrap: wrap;

        .left-column {
            margin: auto;
            margin-bottom: 10px;
        }

        .right-column {
            margin-left: 0;

            .border {
                max-height: 555px;
            }
        }
    }

    @media (max-width: 340px) {
        .right-column {
            .border {
                max-height: 450px;
            }
        }
    }
}