.header {
    padding: 20px 0;
    display: flex;
    max-width: 1780px;
    margin: auto;
    justify-content: space-between;
    align-items: center;
    
    .greeting {
        color: #08DBFF;
        font-size: 50px;
    }

    &-btn {
        text-decoration: none;
        font-size: 24px;
        color: #08DBFF;
        border: 2px solid #08DBFF;
        display: flex;
        align-items: center;
        padding: 5px 10px;
        border-radius: 8px;
        margin-left: 15px;
        box-shadow: 0 0 5px 4px #241B5E;

        span {
            color: #08DBFF;
        }

        &__img {
            width: 35px;
            height: 35px;
        }
    }

    @media (max-width: 560px) {
        font-size: 20px;
        
        .greeting {
            font-size: 35px;
            word-break: break-word;
        }

        &-btn {
            .header-btn__img {
                width: 25px;
                height: 25px;
            }
        }
    }
}