.registration {
    
    form {
        border-radius: 8px;
        padding: 5px 20px 20px 20px;
        margin-top: 15px;
        background: #241B5E;
        box-shadow: 0 0 5px 2px #08DBFF;

        p {
            color: #fff;
            font-size: 26px;
            text-align: center;
            margin: 5px 0;
        }

        input {
            display: block;
            width: 280px;
            height: 30px;
            font-size: 18px;
            padding-left: 10px;
            padding-right: 5px;
            background: #ffffffe8;
            border: 2px solid #08DBFF;
            margin: 10px 0;
            outline: none;
            box-shadow: 0px 0px 5px 2px #083ee2, inset 0px 0px 3px 2px #083ee2;
            border-radius: 10px;
        }

        input[type='submit'] {
            background: #0adaffcc;
            cursor: pointer;
            padding: 0;
            box-shadow: 0px 0px 5px 2px #083ee2, inset 0px 0px 1px 1px #083ee2;

            &:active {
                background: #08DBFF;
            }
        }
    }
}